@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #000000;
  background-color: #f2f2f2;
  min-width: 320px;
  margin: 0;
  padding: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  position: relative;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}
.advantage {
  position: relative;
  background-color: #81b3d2;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 37px 18px;
}
.advantage_city {
  padding-top: 50px;
}
.advantage_grey {
  background-color: #eeeeee;
  padding: 24px 37px;
}
.advantage__title {
  font-size: 18px;
  line-height: 26px;
  margin: 0;
}
.advantage__border .advantage__title::before {
  position: absolute;
  content: "";
  display: block;
  width: 90%;
  height: 1px;
  background: #d6d6d6;
  top: 0;
  left: 17px;
}
.advantage__border_none {
  padding-top: 24px;
}
.advantage__border_none .advantage__title::before {
  display: none;
}
.advantage__text {
  text-align: center;
  color: #333333;
  margin: 0;
}
.advantage__number {
  padding: 10px 0 18px;
}
.advantage__number_first {
  padding: 11px 0 13px;
}
@media (min-width: 768px) {
  .advantage {
    padding: 52px 27px 29px 27px;
  }
  .advantage_grey {
    width: 190px;
    padding: 41px 33px;
  }
  .advantage_position {
    order: 2;
    padding: 52px 40px 29px 40px;
  }
  .advantage__title {
    font-size: 21px;
    text-align: center;
    width: 150px;
  }
  .advantage__title_second {
    width: 150px;
  }
  .advantage__border .advantage__title::before {
    display: none;
  }
  .advantage__number {
    padding: 14px 0 23px;
  }
  .advantage__number_first {
    padding: 15px 0 23px;
  }
  .advantage__text_fourth {
    width: 160px;
  }
}
@media (min-width: 1200px) {
  .advantage {
    width: 300px;
    padding: 51px 50px;
  }
  .advantage_city {
    width: auto;
    padding: 51px 60px;
  }
  .advantage_position {
    padding: 51px 90px;
  }
  .advantage__text_fourth {
    width: 275px;
  }
}
.advantages {
  display: flex;
  flex-direction: column;
}
.advantages__image {
  height: 260px;
}
@media (min-width: 768px) {
  .advantages {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .advantages__promo {
    display: flex;
    width: 100%;
  }
  .advantages__block {
    display: flex;
  }
}
.button {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 21px;
  font-weight: 700;
  line-height: 26px;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  padding: 16px 0;
  text-align: center;
  background-color: #81b3d2;
  border: none;
  cursor: pointer;
}
.button_search {
  display: block;
  width: 284px;
}
.button_disabled {
  background-color: #e5e5e5;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.button:disabled {
  background-color: #e5e5e5;
}
@media (min-width: 768px) {
  .button_search {
    width: 458px;
  }
}
.form {
  padding-right: 20px;
  padding-left: 20px;
  margin-bottom: 23px;
}
.form__line {
  margin-top: 33px;
  margin-bottom: 14px;
}
.form__line_info {
  display: flex;
  flex-direction: column;
  margin-top: 15px;
  margin-bottom: 17px;
}
.form__item {
  position: relative;
  border: none;
  padding: 0;
  margin: 18px 0 46px 0;
  z-index: 2;
}
.form__item_border::before {
  position: absolute;
  content: "";
  display: block;
  top: -55px;
  right: -20px;
  height: 1px;
  width: 320px;
  background: #dbdbdb;
  z-index: 3;
}
.form__item_border-visit {
  position: relative;
}
.form__item_border-visit::before {
  position: absolute;
  content: "";
  display: block;
  top: -79px;
  right: -20px;
  height: 1px;
  width: 320px;
  background: #dbdbdb;
}
.form__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-align: center;
}
.form__label {
  position: relative;
  color: #333333;
}
.form__label_bold {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: none;
  color: #000000;
  padding-left: 39px;
}
.form__label_info {
  margin-left: 15px;
}
.form__label_radio::before {
  content: "";
  position: absolute;
  left: -27px;
  bottom: -12px;
  background-color: #f2f2f2;
  border-radius: 50%;
  border: 17px solid #f2f2f2;
  width: 16px;
  height: 16px;
  cursor: pointer;
}
.form__label_radio:hover::before {
  border: 17px solid #e5e5e5;
  background-color: #e5e5e5;
}
.form__label_tel,
.form__label_email {
  position: relative;
  z-index: 1;
}
.form__label_tel::before,
.form__label_email::before {
  position: absolute;
  top: 42px;
  right: 21px;
  z-index: 1;
}
.form__label_tel::after,
.form__label_email::after {
  content: "";
  position: absolute;
  background-color: #e6e6e6;
  top: 21px;
  right: 0;
  width: 58px;
  height: 58px;
}
.form__label_tel::before {
  content: url("../img/phone-icon.svg");
}
.form__label_email::before {
  content: url("../img/mail-icon.svg");
}
.form__label_checkbox::before {
  content: "";
  position: absolute;
  left: -26px;
  bottom: -10px;
  background-color: #f2f2f2;
  border: 17px solid #f2f2f2;
  border-radius: 4px;
  width: 12px;
  height: 12px;
  cursor: pointer;
}
.form__label_checkbox:hover::before {
  background-color: #e5e5e5;
  border: 17px solid #e5e5e5;
}
input.form__input-radio[type="radio"]:checked + .form__label_radio::before {
  background-color: #000000;
}
input.form__input-checkbox[type="checkbox"]:checked + .form__label_checkbox::before {
  content: url("../img/tick-icon.svg");
  font-size: 8px;
  display: flex;
  justify-content: center;
}
input.form__input-radio[type="radio"]:focus + .form__label_radio::before,
input.form__input-radio[type="radio"]:active + .form__label_radio::before {
  border-color: #d5d5d5;
  background-color: #c1c1c1;
}
input.form__input-checkbox[type="checkbox"]:focus + .form__label_checkbox::before,
input.form__input-checkbox[type="checkbox"]:active + .form__label_checkbox::before {
  content: url("../img/tick-icon-opacity.svg");
  background-color: #d5d5d5;
  border-color: #d5d5d5;
  font-size: 8px;
}
.form__input_bold {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  color: #000000;
  font-weight: 700;
  height: 58px;
  padding-left: 16px;
  border: none;
  background-color: #f2f2f2;
}
.form__input_bold:hover {
  background-color: #e5e5e5;
}
.form__input_bold:focus {
  background-color: #ffffff;
  border: 4px solid #e5e5e5;
  box-sizing: border-box;
}
.form__input_contacts {
  position: relative;
  padding-top: 0;
  padding-bottom: 0;
}
.form__input_review {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;
  border: none;
  background-color: #f2f2f2;
  width: 249px;
  min-height: 120px;
  margin-top: 20px;
  margin-bottom: 9px;
  padding: 15px;
  outline: none;
  resize: none;
}
.form__input_review:hover {
  background-color: #e5e5e5;
}
.form__input_review:focus,
.form__input_review:active {
  background-color: #ffffff;
  box-shadow: inset 0 0 0 4px #e5e5e5;
}
.form__submit {
  display: flex;
  flex-direction: column;
}
.form__comment {
  font-size: 12px;
  color: rgba(51, 51, 51, 0.6);
  align-self: flex-end;
}
.form__button {
  width: 280px;
}
