    padding-right: 60px;
    padding-left: 60px;
  }
  .form__item {
    width: 100%;
  }
  .form__item_border::before {
    display: none;
  }
  .form__item_radio > .form__title,
  .form__item_checkbox > .form__title {
    margin-bottom: 38px;
  }
  .form__item_user,
