    background: #dbdbdb;
  }
  .form__item_radio::after {
    position: absolute;
    content: "";
    display: block;
    top: -50px;
    left: -36px;
    width: 123px;
    height: 1px;
    background: #dbdbdb;
  }
  .form__item_checkbox {
    position: relative;
    width: 480px;
  }
  .form__item_checkbox::before {
    position: absolute;
    content: "";
    display: block;
    top: -66px;
    right: -84px;
    width: 123px;
    height: 1px;
    background: #dbdbdb;
  }
  .form__item_checkbox::after {
    position: absolute;
    content: "";
    display: block;
    top: -66px;
    left: -84px;
    width: 123px;
    height: 1px;
    background: #dbdbdb;
  }
  .form__item_review {
    position: relative;
  }
  .form__item_review::before {
