    margin-bottom: 0;
  }
  .form__submit {
    flex-direction: revert;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 45px;
  }
  .form__comment {
    font-size: 14px;
    align-self: center;
  }
  .form__line {
    margin-top: 2px;
